import React, { useState } from 'react';
import { GrMoney } from "react-icons/gr";
import { FcMoneyTransfer } from "react-icons/fc";

import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  NavItem,
  Badge,NavLink
} from 'reactstrap';
import Nav from 'react-bootstrap/Nav'
import {Link} from 'react-router-dom';
import "../style.css"
const Navi = (props) => {
  const [isOpen, setIsOpen] = useState(false);
  const toggle = () => setIsOpen(!isOpen);
  return (
    <div>
      <Navbar light color="light"  expand="md">
        <NavbarBrand href="/"><Badge color="secondary"><FcMoneyTransfer></FcMoneyTransfer></Badge></NavbarBrand>
        <NavbarToggler onClick={toggle}  />
        <Collapse isOpen={isOpen} navbar>
          <Nav className="mr-auto" navbar>
            <NavItem >
              <Nav.Link to="addproject" as={Link} className="navi-link1">Proje Ekle</Nav.Link>
            </NavItem>
            <NavItem >
              <Nav.Link to="addprim" as={Link} className="navi-link1" >Prim Ekle</Nav.Link>
            </NavItem>
            <NavItem >
              <Nav.Link to="addtobemoney" as={Link} className="navi-link1">Alınacak Ekle</Nav.Link>
            </NavItem>
            
            <NavItem style={{marginLeft:"200px"}} >
              <NavLink style={{cursor:"default"}}><GrMoney style={{color:"black",marginRight:"5px"}}></GrMoney><Badge color="dark">Alınacaklar={props.info.totalTobemoney} TL</Badge></NavLink>
            </NavItem>
            <NavItem  >
              <NavLink style={{cursor:"default"}}><GrMoney style={{color:"black",marginRight:"5px"}}></GrMoney><Badge color="dark">Toplam={props.info.totalAcoounting} TL</Badge></NavLink>
            </NavItem>            
            <NavItem >
              <Nav.Link to="register" as={Link} style={{color:"black"}}>Kullanıcı ekle</Nav.Link>
            </NavItem>
          </Nav>
          
        </Collapse>
      </Navbar>
    </div>
  );
}

export default Navi;