import { ListGroup, ListGroupItem,Badge } from "reactstrap";
import {Link} from 'react-router-dom';
import "../style.css"
function Category(props) {
  return (
    <div >
        
          <h4><Badge color="dark">Menü</Badge></h4>
        <ListGroup style={{height:"200px"}}  >
          <Link  to="accounting" href="" style={{ textDecoration: 'none' }}><ListGroupItem action style={{backgroundColor:"Muhasebe"===props.info.cate.category? "WhiteSmoke":true,color:"Muhasebe"===props.info.cate.category? "black":true}}  onClick={()=>props.info.changeCategory("Muhasebe")} className="mobile-list">Muhasebe</ListGroupItem></Link>
          <Link  to="project" href="" style={{ textDecoration: 'none'}}><ListGroupItem action style={{backgroundColor:"p"===props.info.cate.category? "WhiteSmoke":true,color:"p"===props.info.cate.category? "black":true}} onClick={()=>props.info.changeCategory("p")}  className="mobile-list">Projeler</ListGroupItem></Link>
          <Link  to="prim" href="" style={{ textDecoration: 'none' }}><ListGroupItem action style={{backgroundColor:"a"===props.info.cate.category? "WhiteSmoke":true,color:"a"===props.info.cate.category? "black":true}} onClick={()=>props.info.changeCategory("a")} className="mobile-list">Prim</ListGroupItem></Link>
          <Link  to="gettobemoney" href="" style={{ textDecoration: 'none' }}><ListGroupItem action style={{backgroundColor:"m"===props.info.cate.category? "WhiteSmoke":true,color:"m"===props.info.cate.category? "black":true}} onClick={()=>props.info.changeCategory("m")} className="mobile-list">Alınacaklar</ListGroupItem></Link>
          <Link  to="getallproject" href="" style={{ textDecoration: 'none' }}><ListGroupItem action style={{backgroundColor:"c"===props.info.cate.category? "WhiteSmoke":true,color:"c"===props.info.cate.category? "black":true}} onClick={()=>props.info.changeCategory("c")} className="mobile-list">Projeleri Düzenle</ListGroupItem></Link>
        </ListGroup>
       
      </div>
    );
  
}
export default Category