import {
  Form,
  FormGroup,
  Label,
  Input,
  Button,
  Card,
  CardBody,
  Badge,
} from "reactstrap";
import React, { useState } from "react";
import alertify from "alertifyjs";

function Addproject(params) {
  const [projectName, setProjectName] = useState("");
  const [income, setIncome] = useState(0);
  const [expense, setExpense] = useState(0);
  const onProjectName = (e) => {
    setProjectName({ projectName: e.target.value });
  };
  const onIncome = (e) => {
    setIncome({ income: e.target.value });
  };
  const onExpense = (e) => {
    setExpense({ expense: e.target.value });
  };
  const alertifys = (res) => {
    if (res.success === false) {
      alertify.error("Boş Alan Bırakma");
    } else {
      alertify.success(res.data.projectName + `${" "}Kayıt Oldu`);
    }
  };
  const onSubmitHandler = async (e) => {
    let url = `http://localhost:5002/api/project/project_add`;
    const response = await fetch(url, {
      method: "POST",
      body: JSON.stringify({
        projectName: projectName.projectName,
        income: income.income,
        expense: expense.expense,
      }),
      headers: { "Content-Type": "application/json" },
    });
    const json = await response.json();
    console.log(json)
    alertifys(json);
    cancelCourse();
  };
  const cancelCourse = () => {
    document.getElementById("create-course-form").reset();
  };
  return (
    <div style={{marginTop:"35px"}}>
      <Badge>Proje Ekle</Badge>
      <Card style={{marginTop:"5px"}}>
        <CardBody>
          <Form id="create-course-form" onSubmit={onSubmitHandler}>
            <FormGroup>
              <Label for="projectName">Proje İsmi</Label>
              <Input
                type="text"
                name="projectName"
                id="projectName"
                placeholder="Proje İsmi Gir"
                onChange={onProjectName}
              ></Input>
            </FormGroup>
            <FormGroup>
              <Label for="income">Gelir</Label>
              <Input
                type="number"
                name="income"
                id="income"
                placeholder="Gelir Gir"
                onChange={onIncome}
              ></Input>
            </FormGroup>
            <FormGroup>
              <Label for="expense">Gider</Label>
              <Input
                type="number"
                name="expense"
                id="expense"
                placeholder="Gider Gir"
                onChange={onExpense}
              ></Input>
            </FormGroup>
            <Button type="submit">Kayıt Et</Button>
          </Form>
        </CardBody>
      </Card>

      <div
        style={{
          backgroundColor: "#e7f3fe",
          borderLeft: "6px solid #2196F3",
          marginTop: "20px",
          height: "50px",
        }}
      >
        <p>
          <strong>Bilgi!</strong> Eklenilen proje <strong>"AKTİF"</strong>{" "}
          olarak Projeleri Düzenle bölümüne kayıt edilir.
        </p>
      </div>
    </div>
  );
}
export default Addproject;
