import {
  Form,
  FormGroup,
  Label,
  Input,
  Button,
  Card,
  CardBody
} from "reactstrap";
import React, { useState } from "react";
import alertify from "alertifyjs";
function Register() {
  const [password, setPass] = useState("");
  const [userName, setUserName] = useState("");
  const alert = (success) => {
    if (success === true) {
      alertify.success("Kullanıcı kayıt oldu");
    } else {
      alertify.error("Tekrar deneyiniz");
    }
  };
  const onSubmitHandler = async (e) => {
    e.preventDefault();
    let url = "http://localhost:5002/api/user/register";
    const response = await fetch(url, {
      method: "POST",
      body: JSON.stringify({ userName: userName, password: password }),
      headers: { "Content-Type": "application/json" },
    });
    const json = await response.json();
    alert(json.success);
    cancelCourse();
  };
  const onUserName = (e) => {
    let name = e.target.value;
    setUserName(name);
  };
  const onPass = (e) => {
    let pass = e.target.value;
    setPass(pass);
  };
  const cancelCourse = () => {
    document.getElementById("create-course-form111").reset();
  };
  return (
    <div
      style={{
        display: "flex",
        marginTop:"37px"
      }}
    >
      <div style={{width:"60px",backgroundColor:"#404040"}}>

      <div class="toptobottom" style={{width:"9px",wordWrap:"break-word",margin:"0 auto",marginTop:"60px",backgroundColor:"#404040"}}>
          <h5 style={{color:"white",fontWeight:"900"}}>KAYIT</h5>
      </div>
      </div>
      

        
      
      <Card>
        <CardBody>
          <Form onSubmit={onSubmitHandler} id="create-course-form111">
            <FormGroup>
              <Label for="exampleName">Kullanıcı Adı</Label>
              <Input
                type="text"
                name="userName"
                id="exampleName"
                placeholder="Kullanıcı Adı"
                onChange={onUserName}
              />
            </FormGroup>
            <FormGroup>
              <Label for="examplePassword">Şifre</Label>
              <Input
                type="password"
                name="password"
                id="examplePassword"
                placeholder="Şifre"
                onChange={onPass}
              />
            </FormGroup>
            <Button>Kayıt Et</Button>
          </Form>
        </CardBody>
      </Card>
    </div>
  );
}
export default Register;
