import { BsFillXSquareFill } from "react-icons/bs";
import { Badge, Table } from "reactstrap";
import "../style.css";
function Gettobemoney(props) {
  return (
    <div style={{ marginTop: "35px" }}>
      <Badge>Alınacaklar</Badge>
      <Table
        hover
        dark
        style={{
          border: "1px solid",
          borderRadius: "10px",
          width: "100%",
          marginTop: "35px",
        }}
        className="table-mobile1"
      >
        <thead>
          <tr>
            <th style={{ width: "200px" }}>Alınacak </th>
            <th style={{ width: "150px" }}>Miktar</th>
            <th style={{ width: "170px" }}>SİL</th>
          </tr>
        </thead>
        <tbody>
          {props.info.data.map((x) => (
            <tr key={x._id} className="mobile-list1">
              <th scope="row">{x.name}</th>
              <td>{x.money}</td>
              <td>
                <BsFillXSquareFill
                  style={{ cursor: "pointer" }}
                  onClick={() => props.info.removeTobeMoney(x._id)}
                ></BsFillXSquareFill>
              </td>
            </tr>
          ))}
        </tbody>
      </Table>
    </div>
  );
}

export default Gettobemoney;
