import React, { useState, useEffect } from "react";
import alertify from "alertifyjs";
import { BsFillXSquareFill } from "react-icons/bs";
import Hr from "./Hr";
import { Table, Button, Badge } from "reactstrap";
import { UncontrolledPopover, PopoverHeader, PopoverBody } from "reactstrap";
import "../style.css";
function Getallproject(params) {
  const [data, setData] = useState([]);
  const [fin, setFin] = useState([]);

  //   const [activ, setActiv] = useState([]);
  //   const [con, setCon] = useState([]);
  useEffect(() => {
    fetchData();
  }, [fin]);
  const alertifys = (res) => {
    if (res.success === false) {
      alertify.error("Durum Zaten Aynı");
    } else {
      alertify.success(res.data.projectName + " Değişti");
    }
  };
  const removeProje = async (id) => {
    let url = `http://localhost:5002/api/project/delete/${id}`;
    const response = await fetch(url, { method: `DELETE` });
    const json = await response.json();
    setFin(fin + 1);
    alertify.error("Proje silindi")
  };
  const fetchData = async () => {
    const abortController = new AbortController();

    try {
      let url = "http://localhost:5002/api/project/projects";
      const response = await fetch(url, { signal: abortController.signal });
      const json = await response.json();
      setData(json.data);
    } catch (error) {
      if (abortController.signal.aborted) {
        console.log("Abort cancelled");
      } else throw error;
    }

    return () => abortController.abort();
  };
  const activePut = async (id) => {
    let url = `http://localhost:5002/api/project/project_active/${id}`;
    const response = await fetch(url, {
      method: "PUT",
      headers: { "Content-Type": "application/json" },
    });
    const json = await response.json();
    setFin(fin + 1);
    alertifys(json);
  };
  const finishPut = async (id) => {
    let url = `http://localhost:5002/api/project/project_finish/${id}`;
    const response = await fetch(url, {
      method: "PUT",
      headers: { "Content-Type": "application/json" },
    });
    const json = await response.json();
    setFin(fin + 1);
    alertifys(json);
  };
  const continuePut = async (id) => {
    let url = `http://localhost:5002/api/project/project_continue/${id}`;
    const response = await fetch(url, {
      method: "PUT",
      headers: { "Content-Type": "application/json" },
    });
    const json = await response.json();
    setFin(fin + 1);
    alertifys(json);
  };
  const controler = (data) => {
    if (data.active === true) {
      return "Aktif";
    } else if (data.finish === true) {
      return "Bitti";
    } else {
      return "Devam";
    }
  };
  const cotraollerState = (data) => {
    if (data.active === true) {
      return "success";
    } else if (data.finish === true) {
      return "danger";
    } else {
      return "info";
    }
  };
  return (
    <div style={{ marginTop: "37px" }} className="row">
      <Badge>Projeleri Düzenle</Badge>
      <Hr></Hr>
      <Table
        hover
        dark
        style={{ borderRadius: "10px", marginTop: "35px" }}
        className="table-mobile"
      >
        <thead>
          <tr>
            <th>#</th>
            <th className="mobile">Proje İsmi</th>
            <th className="mobile">Proje Fiyat</th>
            <th className="mobile">Proje Gider</th>
            <th className="mobile">Durum</th>
            {/* <th>Aktife Ekle</th>
            <th>Devama Ekle</th>
            <th>Bittiye Ekle</th>
            <th>Durum</th> */}
          </tr>
        </thead>
        <tbody>
          {data.map((x) => (
            <tr key={x._id} style={{ cursor: "pointer" }} id={x.stringChar} className="size">
              <th scope="row">
                <Badge color={cotraollerState(x)}>-</Badge>
              </th>
              <td
                className="mobile"
                style={{
                  maxWidth: "150px",
                  wordWrap: "break-word",
                  overflow: "break-word",
                }}
              >
                {x.projectName}
              </td>
              <td className="mobile ">{x.income}</td>
              <td className="mobile ">{x.expense}</td>
              <td className="mobile ">{controler(x)} durumda</td>
              <UncontrolledPopover
                trigger="legacy"
                placement="bottom"
                target={x.stringChar}
              >
                <PopoverHeader>{x.projectName}</PopoverHeader>
                <PopoverBody>
                <Button
                    color="success"
                    style={{
                      backgroundColor: "#2c8841",
                      display: "flex",
                      marginTop:"10px"
                    }}
                    onClick={() => activePut(x._id)}
                  >
                    AKTİF YAP
                  </Button>
                  <Button
                    color="info"
                    style={{
                      backgroundColor: "#2f818e",
                      display: "flex",
                      marginTop:"10px"
                    }}
                    onClick={() => continuePut(x._id)}
                  >
                    {" "}
                    DEVAM YAP
                  </Button>
                  <Button
                    color="danger"
                    style={{
                      backgroundColor: "#ab3d47",
                      display: "flex",
                      marginTop:"10px"
                    }}
                    onClick={() => finishPut(x._id)}
                  >
                    BİTTİ YAP
                  </Button>
                  <BsFillXSquareFill
                    style={{
                      cursor: "pointer",
                      display: "flex",
                      marginTop:"10px",
                    }}
                    size="38"
                    onClick={() => removeProje(x._id)}
                  ></BsFillXSquareFill>
                </PopoverBody>
                  
               
              </UncontrolledPopover>
            </tr>
          ))}
        </tbody>
      </Table>
    </div>
  );
}
export default Getallproject;
