import {
  Form,
  FormGroup,
  Label,
  Input,
  Button,
  Card,
  CardBody,Badge
} from "reactstrap";
import React, { useState } from "react";
import alertify from "alertifyjs";
function Tobemoney() {
  const [name, setName] = useState("");
  const [money, setMoney] = useState(0);
  const onName = (e) => {
    setName({ name: e.target.value });
  };
  const onMoney = (e) => {
    setMoney({ money: e.target.value });
  };

  const alertifys = (res) => {
    if (res.success === false) {
      alertify.error("Boş Alan Bırakma");
    } else {
      alertify.success(res.data.name + `${" "}Kayıt Oldu`);
    }
  };
  
  const onSubmitHandler = async (e) => {
      e.preventDefault()
    let url = `http://localhost:5002/api/tobemoney/add`;
    const response = await fetch(url, {
      method: "POST",
      body: JSON.stringify({
        name: name.name,
        money: money.money,
      }),
      headers: { "Content-Type": "application/json" },
    });
    const json = await response.json();
    setMoney({money:0})
    setName({name:""})
    alertifys(json);
    cancelCourse();
  };
  const cancelCourse = () => {
    document.getElementById("create-course-form").reset();
  };
  return (
    <div style={{ marginTop: "35px" }}>
     <Badge>Alınacaklar Ekle</Badge> 
      <Card style={{ marginTop: "5px" }}>
        <CardBody>
          <Form id="create-course-form" onSubmit={onSubmitHandler}>
            <FormGroup>
              <Label for="name">Alınacak İsim</Label>
              <Input
                type="text"
                name="name"
                id="name"
                placeholder="Alınacak İsmi Gir"
                onChange={onName}
              ></Input>
            </FormGroup>
            <FormGroup>
              <Label for="money">Miktar</Label>
              <Input
                type="number"
                name="money"
                id="money"
                placeholder="Miktar Gir"
                onChange={onMoney}
              ></Input>
            </FormGroup>
            <Button type="submit">Kayıt Et</Button>
          </Form>
        </CardBody>
      </Card>

      <div
        style={{
          backgroundColor: "#e7f3fe",
          borderLeft: "6px solid black",
          marginTop: "20px",
          height: "50px",
        }}
      >
        <p>
          <strong>Bilgi!</strong> Eklenilen alınacaklar{" "}
          <strong>"ALINACAKLARA"</strong> Eklenir
        </p>
      </div>
    </div>
  );
}
export default Tobemoney;
