import { Container, Row, Col } from "reactstrap";
import { Switch, Route } from "react-router-dom";
import Navi from "./components/Navi";
import alertify from "alertifyjs";
import { Form, FormGroup, Label, Input, Button } from "reactstrap";
import React, { useState, useEffect } from "react";
import { useCookies } from "react-cookie";
import Category from "./components/Category";
import Accounting from "./components/Accounting";
import Project from "./components/Project";
import Prim from "./components/Prim";
import Addproject from "./components/Addproject";
import Getallproject from "./components/Getallproject";
import Getallprim from "./components/Getallprim";
import Tobemoney from "./components/Tobemoney";
import Gettobemoney from "./components/Gettobemoney";
import Home from "./components/Home";
import Register from "./components/Register";
function App() {
  const [data, setData] = useState([]);
  const [data1, setData1] = useState([]);
  const [email, setEmail] = useState("");
  const [fin, setFin] = useState(0);
  const [password, setpass] = useState("");
  const [cookie, setCookie] = useCookies(["user"]);
  console.log(cookie.s)
  const [cate,setCate]=useState("");
  const changeCategory=(x)=>{
    setCate({category:x})
    setFin(fin+1)
  }
  let infoCategory={
    changeCategory:changeCategory,
    cate:cate
  }
  const alertifys = (res) => {
    if (res.success === false) {
      alertify.error("Kullanıcı veya parola yanlış");
    }
  };
  const onchangeHandlerPass = (e) => {
    let pass = e.target.value;
    setpass(pass);
  };
  const onchangeHandlerEma = (e) => {
    let value = e.target.value;
    setEmail(value);
  };
  function getCookie(name) {
    const value = `; ${document.cookie}`;
    const parts = value.split(`; ${name}=`);
    if (parts.length === 2) return parts.pop().split(";").shift();
  }
  const onSubmitHandler = async (e) => {
    e.preventDefault();

    const response = await fetch("http://localhost:5002/api/user/login", {
      method: "POST",
      body: JSON.stringify({ userName: email, password: password }),
      headers: { "Content-Type": "application/json" },
    });
    const json = await response.json();
    const data = json.token;
    if (json.success === false) {
      alertifys(json);
    }
    if (data) {
      setCookie("user", data, {
        path: "/",
        maxAge: 360000 * 10,
        sameSite: true,
      });
    }
  };
  useEffect(() => {
    const abortController = new AbortController();
    const fetchData = async () => {
      try {
        let url = "http://localhost:5002/api/tobemoney/get";
        const response = await fetch(url, { signal: abortController.signal });
        const json = await response.json();
        setData(json.data);
      } catch (error) {
        if (abortController.signal.aborted) {
          console.log("Abort cancelled");
        } else throw error;
      }
    };

    fetchData();

    return () => abortController.abort();
  }, [fin]);
  useEffect(() => {
    const abortController = new AbortController();
    const fetchData = async () => {
      try {
        let url = "http://localhost:5002/api/accounting/accountings";
        const response = await fetch(url, { signal: abortController.signal });
        const json = await response.json();
        setData1(json.data);
      } catch (error) {
        if (abortController.signal.aborted) {
          console.log("Abort cancelled");
        } else throw error;
      }
    };

    fetchData();

    return () => abortController.abort();
  }, []);
  let infoAccounting={
    data:data1
  }
 
  let total1 = [];
  data.map((x) => total1.push(x.money));
  var totalTobemoney = 0;
  for (var i = 0; i < total1.length; i++) {
    if (isNaN(total1[i])) {
      continue;
    }

    totalTobemoney += Number(total1[i]);
  }
  //total accounting
  let totalListAccounting = [];
  data1.map((x) => totalListAccounting.push(x.total));
  var totalAcoounting = 0;
  for (var a = 0; a < totalListAccounting.length; a++) {
    if (isNaN(totalListAccounting[a])) {
      continue;
    }

    totalAcoounting += Number(totalListAccounting[a]);
  }
  let infotTotal={
    totalAcoounting:totalAcoounting,
    totalTobemoney:totalTobemoney
  }
  const removeTobeMoney = async (id) => {
    let url = `http://localhost:5002/api/tobemoney/delete/${id}`;
    const response = await fetch(url, {
      method: "DELETE",
    });
    const json = await response.json();
    if (json.success === true) {
      alertify.error("Silindi");
      setFin(fin + 1);
    }
  };
  let infoGetToBeMoney={
    removeTobeMoney:removeTobeMoney,
    data:data
  }

  if (!getCookie("user")) {
    return (
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          height: "100vh",
        }}
      >
        <Form onSubmit={onSubmitHandler}>
          <FormGroup>
            <Label for="exampleName">Kullanıcı Adı</Label>
            <Input
              type="text"
              name="userName"
              id="exampleName"
              placeholder="Kullanıcı Adı"
              onChange={onchangeHandlerEma}
            />
          </FormGroup>
          <FormGroup>
            <Label for="examplePassword">Şifre</Label>
            <Input
              type="password"
              name="password"
              id="examplePassword"
              placeholder="Şifre"
              onChange={onchangeHandlerPass}
            />
          </FormGroup>
          <Button>Giriş</Button>
        </Form>
      </div>
    );
  } else {
    return (
      <div className="App">

        <Container>
          <Navi info={infotTotal}></Navi>

          <Row>
            <Col xs="3">
              <Category info={infoCategory}></Category>
            </Col>
            <Col xs="9">
              <Switch>
                <Route
                  exact
                  path="/accounting"
                  render={(props) => <Accounting info={infoAccounting}></Accounting>}
                />
                <Route
                  exact
                  path="/project"
                  render={(props) => <Project></Project>}
                />
                <Route
                  exact
                  path="/prim"
                  render={(props) => <Getallprim></Getallprim>}
                />
                <Route
                  exact
                  path="/addproject"
                  render={(props) => <Addproject></Addproject>}
                />
                <Route
                  exact
                  path="/getallproject"
                  render={(props) => <Getallproject></Getallproject>}
                />
                
                <Route
                  exact
                  path="/addprim"
                  render={(props) => <Prim></Prim>}
                />
                <Route
                  exact
                  path="/addtobemoney"
                  render={(props) => <Tobemoney></Tobemoney>}
                />
                <Route
                  exact
                  path="/gettobemoney"
                  render={(props) => <Gettobemoney info={infoGetToBeMoney}></Gettobemoney>}
                />
                <Route
                  exact
                  path="/"
                  render={(props) => <Home ></Home>}
                />
                <Route
                  exact
                  path="/register"
                  render={(props) => <Register ></Register>}
                />
              </Switch>
            </Col>
          </Row>
        </Container>      
      </div>
    );
  }
}
export default App;
