import React, { useState, useEffect } from "react";
import { FcRightDown2 } from "react-icons/fc";
import { Table, Badge } from "reactstrap";



function Project() {
  const [active,setActive ] = useState([]);
  const [continues,setContinue ] = useState([]);
  const [finish,setFinish ] = useState([]);
  useEffect(() => {
    getActive();
    getContinue();
    getFinish()
  },[]); 
  const getActive = async () => {
    let url = "http://localhost:5002/api/project/activeget";
    const response = await fetch(url);
    const json = await response.json();
    setActive(json.data);
  };
  const getContinue = async () => {
    let url = "http://localhost:5002/api/project/continueget";
    const response = await fetch(url);
    const json = await response.json();
    setContinue(json.data);
  };
  const getFinish = async () => {
    let url = "http://localhost:5002/api/project/finishget";
    const response = await fetch(url);
    const json = await response.json();
    setFinish(json.data);
  };
  return (
    <div className="row" style={{marginTop:"30px",columnGap:"15px"}}>
      <div className="column"  style={{columnWidth:"250px"}}>
        <h1>
          {" "}
          <Badge color="success" style={{width:"250px",backgroundColor:"#5bad6d"}}>AKTİF PROJE</Badge>
        </h1>
        <Table dark className="table table-striped table-bordered"
        >
          <thead>
            <tr>
              <th style={{textAlign:"center"}}>
                <Badge>Proje İsmi</Badge><FcRightDown2 ></FcRightDown2>
              </th>
            </tr>
          </thead>
          <tbody >
              {active.map(x=>(
                  <tr key={x._id}>
                  <th scope="row" style={{maxWidth:"250px",wordWrap:"break-word",overflow:"break-word"}}>{x.projectName}</th>
                </tr>
              ))}
            
            
          </tbody>
        </Table>
      </div>
      <div className="column" style={{columnWidth:"250px"}}>
        <h1>
          {" "}
          <Badge color="primary" style={{width:"250px",backgroundColor:"#4c79a9"}}>DEVAM EDEN</Badge>
        </h1>
        <Table dark className="table table-striped table-bordered"
        >
          <thead>
            <tr>
              <th style={{textAlign:"center"}}>
                <Badge >Proje İsmi</Badge><FcRightDown2 ></FcRightDown2>
              </th>
            </tr>
          </thead>
          <tbody className="list-group1">
          {continues.map(x=>(
                  <tr key={x._id}>
                  <th scope="row" style={{maxWidth:"250px",wordWrap:"break-word",overflow:"break-word"}}>{x.projectName}</th>
                </tr>
              ))}
          </tbody>
        </Table>
      </div>
      <div className="column" style={{columnWidth:"250px"}}>
        <h1>
          {" "}
          <Badge color="danger" style={{width:"250px",backgroundColor:"#c75b65"}}>BİTEN PROJE</Badge>
        </h1>
        <Table dark className="table table-striped table-bordered"
        >
          <thead>
            <tr>
              <th style={{textAlign:"center"}}>
                <Badge >Proje İsmi</Badge><FcRightDown2 ></FcRightDown2>
              </th>
            </tr>
          </thead>
          <tbody className="list-group1">
          {finish.map(x=>(
                  <tr key={x._id}>
                  <th scope="row" style={{maxWidth:"250px",wordWrap:"break-word",overflow:"break-word"}}>{x.projectName}</th>
                </tr>
              ))}
          </tbody>
        </Table>
      </div>
    </div>
  );
}
export default Project;
