import React, { useState } from "react";
import { Label, FormGroup, Input, Form, Button ,CardBody,Card,Badge} from "reactstrap";
import alertify from "alertifyjs";

function Prim() {
  const [dateget, setDate] = useState();
  const [name, setName] = useState();
  const [money, setMoney] = useState();
  const [note, setNote] = useState();
  const [project, setProject] = useState();
  const alertifys = (res) => {
    if (res.success === false) {
      alertify.error("Boş Alan Bırakma");
    } else {
      alertify.success(res.data.project + `${" "}Kayıt Oldu`);
    }
  };
  const onSubmitHandler = async (e) => {
    e.preventDefault();
    let url = `http://localhost:5002/api/prim/add`;
    const response = await fetch(url, {
      method: "POST",
      body: JSON.stringify({
        name: name.name,
        money: money.money,
        note: note.note,
        project: project.project,
        createAt: dateget.date,
      }),
      headers: { "Content-Type": "application/json" },
    });
    const json = await response.json();
    alertifys(json);
    cancelCourse()
  };
  const cancelCourse = () => {
    document.getElementById("create-course-form1").reset();
  };
  const date = (e) => {
    setDate({ date: e.target.value });
  };
  const onName = (e) => {
    setName({ name: e.target.value });
  };
  const onProject = (e) => {
    setProject({ project: e.target.value });
  };
  const onMoney = (e) => {
    setMoney({ money: e.target.value });
  };
  const onNote = (e) => {
    setNote({ note: e.target.value });
  };

  return (
    <div style={{marginTop:"35px"}}>
      <Badge>Prim Ekle</Badge>
      <Card style={{marginTop:"5px"}}>
        <CardBody>
        <Form onSubmit={onSubmitHandler} id="create-course-form1">
        <FormGroup>
          <Label for="date">Tarih</Label>
          <Input type="datetime-local" name="date" onChange={date} />
        </FormGroup>
        <FormGroup>
          <Label for="name">İsim</Label>
          <Input type="text" name="name" onChange={onName} placeholder="İsim Gir"/>
        </FormGroup>
        <FormGroup>
          <Label for="money">Fiyat</Label>
          <Input type="text" name="money" onChange={onMoney} placeholder="Fiyat Gir"/>
        </FormGroup>
        <FormGroup>
          <Label for="project">Proje İsmi</Label>
          <Input type="text" name="project" onChange={onProject} placeholder="Proje İsmi Gir" />
        </FormGroup>
        <FormGroup>
          <Label for="note">Not</Label>
          <Input type="textarea" name="note" onChange={onNote} placeholder="Note Gir" />
        </FormGroup>
        <Button type="submit">Kayıt</Button>
      </Form>
        </CardBody>
      </Card>
      
    </div>
  );
}
export default Prim;
