import alertify from "alertifyjs";
import { BsFillXSquareFill } from "react-icons/bs";

import React, { useState } from "react";
import { Table, Button, Collapse, Card, CardBody,Badge } from "reactstrap";
import { Form, FormGroup, Label, Input } from "reactstrap";
import Hr from "../components/Hr";
import "../style.css"

function Accounting(props) {
  const [incomeName, setIncomeName] = useState("");
  const [income, setIncome] = useState(0);
  const [expense, setExpense] = useState(0);
  const [isOpen, setIsOpen] = useState(false);
  const toggle = () => setIsOpen(!isOpen);

 

  const removeCard = async (id) => {
    let url = `http://localhost:5002/api/accounting/delete/${id}`;
    const response = await fetch(url, { method: `DELETE` });
    const json = await response.json();
    alertify.error(json.message);
  };
  
  const alertifys = (res) => {
    if (res.success === false) {
      alertify.error("Boş Alan Bırakma");
    } else {
      alertify.success(res.data.incomeName + `${" "}Kayıt Oldu`);
    }
  };
  const onIncomeName = (e) => {
    setIncomeName({ incomeName: e.target.value });
  };
  const onIncome = (e) => {
    setIncome({ income: e.target.value });
  };
  const onExpense = (e) => {
    setExpense({ expense: e.target.value });
  };
  const onSubmitHandler = async (e) => {
    let url = `http://localhost:5002/api/accounting/add`;
    const response = await fetch(url, {
      method: "POST",
      body: JSON.stringify({
        incomeName: incomeName.incomeName,
        income: income.income,
        expense: expense.expense,
      }),
      headers: { "Content-Type": "application/json" },
    });
    const json = await response.json();
    alertifys(json);
    cancelCourse();
  };
  const cancelCourse = () => {
    document.getElementById("create-course-form").reset();
  };
  const filterTodos = (e) => {
    const eTarget = e.target.value.toLowerCase().trim();
    const listItems = document.querySelectorAll(".list11");
    listItems.forEach(function (listItem) {
      const text = listItem.textContent.toLowerCase().trim();
      if (text.indexOf(eTarget) === -1) {
        listItem.setAttribute("style", "display : none !important");
      } else {
        listItem.setAttribute("style", "display : table-row");
      }
    });
  };
  return (
    <div style={{marginTop:"35px"}}>
      <Badge>Muhasebe Ekle ve Kayıtlar</Badge> 
      <div style={{marginTop:"35px"}}>
        <Button
          color="secondary"
          onClick={toggle}
          style={{ marginBottom: "1rem",width:"100%" }}
        >
          Kayıt Tablosunu Açmak İçin Tıklayınız
        </Button>
        <Collapse isOpen={isOpen}>
          <Card>
            <CardBody>
              <Form onSubmit={onSubmitHandler} id="create-course-form">
                <FormGroup>
                  <Label for="incomeName">Kayıt İsmi</Label>
                  <Input
                    type="text"
                    name="incomeName"
                    id="incomeName"
                    placeholder="Kayıt Gir"
                    onChange={onIncomeName}
                  ></Input>
                </FormGroup>
                <FormGroup>
                  <Label for="income">Gelir</Label>
                  <Input
                    type="number"
                    name="income"
                    id="income"
                    placeholder="Gelir Gir"
                    onChange={onIncome}
                  ></Input>
                </FormGroup>
                <FormGroup>
                  <Label for="expense">Gider</Label>
                  <Input
                    type="number"
                    name="expense"
                    id="expense"
                    placeholder="Gider Gir"
                    onChange={onExpense}
                  ></Input>
                </FormGroup>

                <Button type="submit">Kayıt Et</Button>
              </Form>
            </CardBody>
          </Card>
        </Collapse>
      </div>

      <Hr></Hr>
      <Input
        type="text"
        name="filter"
        id="filter"
        placeholder="Ara"
        style={{ marginTop: "15px", marginBottom: "15px" }}
        onChange={filterTodos}
      />
      <Hr></Hr>
        <Table style={{ marginTop: "15px" }} className="table-mobile3">
        <thead>
          <tr>
            <th>İsim</th>
            <th>Gelir</th>
            <th>Gider</th>
            <th>Kalan</th>
            <th>Tarih</th>
            <th>Sil</th>
          </tr>
        </thead>
        <tbody className="list-group1">
          {props.info.data.map((x) => (
            <tr key={x._id} className="list11">
              <th scope="row">{x.incomeName}</th>
              <td>{x.income} TL</td>
              <td>{x.expense} TL</td>
              <td>{x.total} TL</td>
              <td>{x.createAtReal} </td>
              <td>
                 <BsFillXSquareFill style={{cursor:"pointer"}} onClick={() => removeCard(x._id)}>
                  
                </BsFillXSquareFill> 
              </td>
            </tr>
          ))}
        </tbody>
      </Table>
      
    </div>
  );
}
export default Accounting;
