import React, { useState, useEffect } from "react";
import alertify from "alertifyjs";
import { BsFillXSquareFill } from "react-icons/bs";

import {
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Table,
  Input,
  Card,
  CardBody,
  Badge,
} from "reactstrap";
import {
  Button,
  UncontrolledPopover,
  PopoverHeader,
  PopoverBody,
} from "reactstrap";
import "../style.css"
function Getallprim() {
  const [data, setData] = useState([]);
  const [fin, setFin] = useState(0);
  const [lastClicked, setLastClicked] = useState({});
  const [dropdownOpen, setDropdownOpen] = useState(false);
  const toggle = () => setDropdownOpen((prevState) => !prevState);
  const alertifys = (res) => {
    if (res.success === false) {
      alertify.error("Boş Alan Bırakma");
    } else {
      alertify.error(res.message);
    }
  };
  const filterTodos = (e) => {
    const eTarget = e.target.value.toLowerCase().trim();
    const listItems = document.querySelectorAll(".listname");
    listItems.forEach(function (listItem) {
      const text = listItem.textContent.toLowerCase().trim();
      if (text.indexOf(eTarget) === -1) {
        listItem.setAttribute("style", "display : none !important");
      } else {
        listItem.setAttribute("style", "display : table-row");
      }
    });
  };
  const getMontPrim = async (id) => {
    let url = `http://localhost:5002/api/prim/get/`;
    if (id) {
      url += id;
    }
    const response = await fetch(url);
    const json = await response.json();
    setData(json.data);
  };
  const removePrim = async (id) => {
    let url = `http://localhost:5002/api/prim/delete/${id}`;
    const response = await fetch(url, { method: "DELETE" });
    const json = await response.json();
    setFin(fin + 1);
    alertifys(json);
    console.log(json);
  };
  useEffect(() => {
    getMontPrim(lastClicked.no);
  }, [lastClicked, fin]);

  return (
    <div style={{marginTop:"35px"}}>
      <Badge>Prim</Badge>
      <Card style={{ marginTop: "5px" }}>
        <CardBody
          style={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            height: "10vh",
          }}
        >
          <div
            className="row"
            style={{
              marginLeft: "1px",
              marginTop: "35px",
              marginBottom: "20px",
              columnGap: "15px",
            }}
          >
            <div className="column">
              <Input
                type="text"
                name="filter"
                id="filter"
                placeholder="Ara"
                onChange={filterTodos}
              />
            </div>
            <div className="column">
              <Dropdown isOpen={dropdownOpen} toggle={toggle} direction="right">
                <DropdownToggle caret>
                  {lastClicked.name || "Tarihe Göre"}
                </DropdownToggle>
                <DropdownMenu container="body">
                  <DropdownItem onClick={() => getMontPrim()}>
                    HEPSİ
                  </DropdownItem>
                  <DropdownItem
                    onClick={() => setLastClicked({ no: 1, name: "Ocak" })}
                  >
                    Ocak
                  </DropdownItem>
                  <DropdownItem
                    onClick={() => setLastClicked({ no: 2, name: "Şubat" })}
                  >
                    Şubat
                  </DropdownItem>
                  <DropdownItem
                    onClick={() => setLastClicked({ no: 3, name: "Mart" })}
                  >
                    Mart
                  </DropdownItem>
                  <DropdownItem
                    onClick={() => setLastClicked({ no: 4, name: "Nisan" })}
                  >
                    Nisan
                  </DropdownItem>
                  <DropdownItem
                    onClick={() => setLastClicked({ no: 5, name: "Mayıs" })}
                  >
                    Mayıs
                  </DropdownItem>
                  <DropdownItem
                    onClick={() => setLastClicked({ no: 6, name: "Haziran" })}
                  >
                    Haziran
                  </DropdownItem>
                  <DropdownItem
                    onClick={() => setLastClicked({ no: 7, name: "Temmuz" })}
                  >
                    Temmuz
                  </DropdownItem>
                  <DropdownItem
                    onClick={() => setLastClicked({ no: 8, name: "Ağustos" })}
                  >
                    Ağustos
                  </DropdownItem>
                  <DropdownItem
                    onClick={() => setLastClicked({ no: 9, name: "Eylül" })}
                  >
                    Eylül
                  </DropdownItem>
                  <DropdownItem
                    onClick={() => setLastClicked({ no: 10, name: "Ekim" })}
                  >
                    Ekim
                  </DropdownItem>
                  <DropdownItem
                    onClick={() => setLastClicked({ no: 11, name: "Kasım" })}
                  >
                    Kasım
                  </DropdownItem>
                  <DropdownItem
                    onClick={() => setLastClicked({ no: 12, name: "Aralık" })}
                  >
                    Aralık
                  </DropdownItem>
                </DropdownMenu>
              </Dropdown>
            </div>
          </div>
        </CardBody>
      </Card>
          <Table hover dark style={{ borderRadius: "10px" ,marginTop:"35px",width:"100%"}} className="table-mobile2">
            <thead>
              <tr>
                <th className="mobile">Proje</th>
                <th className="mobile">İsim</th>
                <th className="mobile">Ücret</th>
                <th className="mobile">Tarih</th>
                <th className="mobile">NOT</th>
                <th className="mobile">SİL</th>
              </tr>
            </thead>
            <tbody>
              {data.map((x) => (
                <tr key={x._id} className="listname">
                  <th scope="row" className="mobile">{x.project}</th>
                  <td className="mobile">{x.name}</td>
                  <td className="mobile">{x.money}</td>
                  <td className="mobile">{x.yearDate}</td>
                  <td className="mobile ">
                    <Button id={x.stringChar} type="button" className="icon-btn" >
                    <h5 className="icon-btn">Not</h5>
                    </Button>{" "}
                    <UncontrolledPopover
                      trigger="legacy"
                      placement="bottom"
                      target={x.stringChar}
                    >
                      <PopoverHeader>{x.project}</PopoverHeader>
                      <PopoverBody>{x.note}</PopoverBody>
                    </UncontrolledPopover>
                  </td>
                  <td className="mobile">
                    <BsFillXSquareFill
                      style={{ cursor: "pointer" }}
                      onClick={() => removePrim(x._id)}
                    >
                      sil
                    </BsFillXSquareFill>
                  </td>
                </tr>
              ))}
            </tbody>
          </Table>
    </div>
  );
}

export default Getallprim;
